<template>
  <h1>hello address</h1>
</template>

<script>
import {cityGuess,hotcity,groupcity} from '../../service/getData'
export default {
  data(){
    return {
      localCity:{},
      hotcity:[],
      groupcity:[]
    }
  },
  mounted(){
    this.getData()
  },
  methods:{
    async getData(){
      this.localCity = await cityGuess()
      this.hotcity = await hotcity()
      this.groupcity = await groupcity()
    }
  }
}
</script>

